<template>
  <div style="width: 100%; height: 100vh; background: darkslateblue; overflow: hidden">
    <div style="width: 400px; margin: 150px auto">
      <div style="color: #cccccc; font-size: 30px; text-align: center">欢迎注册</div>

      <el-form ref="form" :rules="rules" :model="form" size="normal" style="margin-top: 20px">
        <el-form-item prop="username">
          <el-input prefix-icon="el-icon-user-solid" v-model="form.username"></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input prefix-icon="el-icon-lock" v-model="form.password" show-password></el-input>
        </el-form-item>
        <el-form-item prop="confirm">
          <el-input prefix-icon="el-icon-lock" v-model="form.confirm" show-password></el-input>
        </el-form-item>
        <el-form-item>
          <el-button style="width: 100%" type="primary" @click="register">注册</el-button>
        </el-form-item>
      </el-form>
    </div>

  </div>
</template>

<script>
import request from "../utils/request";

export default {
  name: "Login",
  data() {
    return {
      form: {},
      rules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ],
        confirm: [
          { required: true, message: '请输入密码', trigger: 'blur' }
        ],
      }
    }
  },
  methods: {
    register() {
      if (this.form.password !== this.form.confirm) {
        this.$message({
          type: "error",
          message: "两次密码输入不一致"
        })
        return
      }
      this.$refs['form'].validate((valid) => {
        if (valid) { //输入用户名和密码才会发送请求
          request.post("/api/user/register", this.form).then(res => {
            if(res.resultCode === 0) {
              this.$message({
                type: "success",
                message: "注册成功"
              })
              this.$router.push("/") //登录成功后进行页面跳转
            } else {
              this.$message({
                type: "error",
                message: res.message
              })
            }
          })
        }
      })
    }
  }
}
</script>

